import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Chip } from '@mui/material';
import Modal from 'react-bootstrap/Modal'
import ProgressBar from 'react-bootstrap/ProgressBar'
import Footer from '../Footer';
import db from 'libs/db';
import CurrencyInput from 'react-currency-input-field';

export default function MonthlyExpense(props) {

	const [monthlyExpense, setMonthlyExpense] = React.useState();
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const [showTipModal,setShowTipModal] = React.useState(false);
	const STATE = props.location.state

	const chipset = [
        { key: 50000, name: "₹ 50,000" },
		{ key: 75000, name: "₹ 75,000" },
		{ key: 100000, name: "₹ 1,00,000" },
    ]

	React.useEffect(async ()=>{
		await db.get(db.model.createGoal).then(p=>{
			if(p && p.monthlyExpense!==undefined){
				setMonthlyExpense(p.monthlyExpense)
			} else {
				setMonthlyExpense("")
			}
		})
	},[])

	const checkValidation = () => {
		if (monthlyExpense !== '' && monthlyExpense > 0) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [monthlyExpense])

	const TipModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showTipModal}
				onHide={() => setShowTipModal(false)}
				keyboard={false}
			>
				<Modal.Body>
					<div className="bodyTextDark">
						Include only those expenses that will continue even after you retire.
					</div>
					<div className="bodyTextDark" style={{paddingBottom:"4%"}}>
						For example - grocery, electricity bill etc.
					</div>
					<div className="bodyTextDark">
						Exclude expenses that you will not have in your reitrement.
					</div>
					<div className="bodyTextDark" style={{paddingBottom:"4%"}}>
						For example - home loan EMI.
					</div>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => setShowTipModal(false)}>
						Okay
					</button>
				</Modal.Footer>
			</Modal>
		)
	}

	if (STATE === undefined) {
		props.history.push('/Goals')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header={STATE.header}
				navigation={props.history}
				right= 'tip'
				rightAction={()=>setShowTipModal(true)}
			/>
			<ProgressBar now={40} className="progress-bar-custom" />
			<div className='dataContainer'>
				<div className="bodyTextDark" style={{ marginTop: "6%" }}>What is your present monthly expense?</div>
				<CurrencyInput
					className='normalInput rupee'
					id="monthlyExpense"
					name="monthlyExpense"
					placeholder='40,000'
					value={monthlyExpense}
					allowDecimals={false}
					allowNegativeValue={false}
					disableAbbreviations={false}
					prefix=" "
					onValueChange={(value) => setMonthlyExpense(Number(value) || '')}
					intlConfig={{ locale: 'en-IN', currency: 'INR' }}
					autoFocus
				/>
				<div className='hiddden-scrollbar'>
					{chipset.map((item) => {
						return (
							<Chip
								className={monthlyExpense === item.key ? "normalChip selected" : "normalChip"}
								key={item.key}
								label={item.name}
								onClick={() => {
									setMonthlyExpense(item.key)
								}}
							/>
						)
					})}
				</div>
			</div>
			{/* <div className='footer'>
				<button
					className='solidButton'
					style={{ opacity: nextDisabled ? 0.4 : 1 }}
					onClick={() => {
						if(!nextDisabled){
							props.history.push({ pathname: '/Goals/invest-today', state: { ...STATE, monthlyExpense, presentCost: monthlyExpense*12*STATE.goalDue }})
						}
					}}
				>Confirm</button>
			</div> */}
			<Footer
				navigation={props.history}
				next='/Goals/invest-today'
				state={{ ...STATE, monthlyExpense}}
				saveData = {{ monthlyExpense }} 
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
			<TipModal />
		</div>
	)
}
